import {
    IEmailConfigurationView,
    IEmailConfigurationController
} from "../../contracts/Portal_Configuration/EmailConfigurationContract";

import axios from "../../plugins/axios"


export class EmailConfigurationController implements IEmailConfigurationController {

    _view: IEmailConfigurationView

    constructor(view: IEmailConfigurationView) {
        this._view = view
    }

    get(): void {
        axios.get('EmailConfiguration').then((Response: any) => {
            if (Response.data && Response.data.passEmailFrom) {
                this._view.update(Response.data.emailFrom, atob(Response.data.passEmailFrom), Response.data.smtp, Response.data.port)
            } else {
                this._view.update("", "", "", "")
            }
        }).catch((e: any) => {
            this._view.showMsg("Náo foi possível carregar as informações.", 1)
            console.log(e)
        })
    }

    salvar(email: String, pass: String, smtp: String, port: String): void {
        axios.post('EmailConfiguration', {
            "emailFrom": email,
            "passEmailFrom": pass,
            "smtp": smtp,
            "port": port
        }).then((Response: any) => {
            console.log(Response)
            if (Response.data.sucesso) {
                this._view.showMsg("Salvo com sucesso.", 1)
            } else {
                this._view.showMsg(Response.data.message, 0)
            }
        }).catch((e: any) => {
            this._view.showMsg("Náo foi possível carregar as informações.", 1)
            console.log(e)
        })
    }



}