






























































import Vue from "vue";
import KLoading from "../../components/KLoading.vue";
import KDialog from "../../components/KDialog.vue";
import KDataList from "../../components/KDataList.vue";

import { Display } from "../../configuration/display";

import {
  IEmailConfigurationView,
  IEmailConfigurationController
} from "../../contracts/Portal_Configuration/EmailConfigurationContract";

import { EmailConfigurationController } from "../../controllers/Portal_Configuration/EmailConfigurationController";

import Component from "vue-class-component";

@Component({
  components: {
    KLoading,
    KDialog,
    KDataList
  }
})
export default class EmailConfiguration extends Vue
  implements IEmailConfigurationView {
  _controller?: IEmailConfigurationController;

  dialogLoading = false;
  dialogMessage = false;
  textError = "";

  visiblePass = false;

  sairMessage = false;

  email = {
    passEmailFrom: "",
    emailFrom: "",
    smtp: "",
    port: ""
  };

  loading(visible: boolean): void {
    this.dialogLoading = visible;
  }
  showMsg(msg: string, modo: number): void {
    this.textError = msg;
    this.dialogMessage = true;
    this.sairMessage = modo == 1;
  }

  fecharDialogMessage() {
    this.dialogMessage = false;
    if (this.sairMessage) {
      this.$router.push("/");
    }
  }

  isMobile = false;

  save() {
    this._controller!!.salvar(
      this.email.emailFrom,
      this.email.passEmailFrom,
      this.email.smtp,
      this.email.port
    );
  }

  update(email: string, pass: string, smtp: string, port: string): void {
    this.email.emailFrom = email;
    this.email.passEmailFrom = pass;
    this.email.smtp = smtp;
    this.email.port = port;
  }

  created() {
    this.isMobile = Display.isMobile();
    this._controller = new EmailConfigurationController(this);

    this._controller!!.get();
  }
}
